import React, {Component} from 'react';
import PropTypes from 'prop-types';

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class KvideoComp extends Component {

    constructor(props) {
        super(props);
        this.pc = null
    }
    
    negotiate() {
        const pc = this.pc;
        pc.addTransceiver('video', {direction: 'recvonly'});
        return pc.createOffer().then(function(offer) {
            return pc.setLocalDescription(offer);
        }).then(function() {
            // wait for ICE gathering to complete
            return new Promise(function(resolve) {
                if (pc.iceGatheringState === 'complete') {
                    resolve();
                } else {
                    function checkState() {
                        if (pc.iceGatheringState === 'complete') {
                            pc.removeEventListener('icegatheringstatechange', checkState);
                            resolve();
                        }
                    }
                    pc.addEventListener('icegatheringstatechange', checkState);
                }
            });
        }).then(function() {
            var offer = pc.localDescription;
            return fetch('/offer', {
                body: JSON.stringify({
                    sdp: offer.sdp,
                    type: offer.type,
                }),
                headers: {
                    'Content-Type': 'application/json'
                },
                method: 'POST'
            });
        }).then(function(response) {
            return response.json();
        }).then(function(answer) {
            if (pc.signalingState!=='closed')
                pc.setRemoteDescription(answer);
        })
    }

    start() {
        const {id} = this.props;        
        const config = {
            sdpSemantics: 'unified-plan'
        };
        if (1) {//document.getElementById('use-stun').checked) {
            config.iceServers = [{urls: ['stun:stun.l.google.com:19302']}];
        }
        //else
        //    config.iceServers = [{urls: []}];

        //var stream = navigator.mediaDevices.getUserMedia({video: true});
        this.pc = new RTCPeerConnection(config);
        //var videoSender = pc.addTrack(stream.getVideoTracks()[0], stream);

        // connect video
        this.pc.addEventListener('track', function(evt) {
            if (evt.track.kind == 'video') {
                document.getElementById(id).srcObject = evt.streams[0];
            } 
            //console.log('add listener');
            //var video = document.getElementById('video');
            //video.focus();
        });


        this.negotiate();
    }

    stop() {
        if (this.pc!==null)
            this.pc.close();
    }

    componentDidMount() {
        this.start();
    }

    componentWillUnmount() {
        this.stop();
    }

    render() {
        const {id, image, width, height} = this.props;

        //console.log("render:", image);
        return (
            <video width={width.toString()} height={height.toString()} id={id} autoPlay="true" playsInline="true" muted="true"></video>
         );
    }
}

KvideoComp.defaultProps = {};

KvideoComp.propTypes = {

    /**
     * Image 
     */
    image: PropTypes.array,

    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The width used to specify width of video window
     */
    width: PropTypes.number,

    /**
     * The height used to specify height of video window
     */
    height: PropTypes.number,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
